.login{
    display: grid;
    place-items: center;
    height: 100vh;
    background-color: #68c4ac;
}

.login > img {
    width: 55%;
}

.login > a {
    padding: 20px;
    border-radius: 99px;
    background-color: rgb(255,255,255);
    font-weight: 800;
    color: rgb(169,169,169);
    text-decoration: none;
} 